
body{
    margin: 0 15% 0 15%;
    background-color: rgb(255, 255, 255);
}

p {
    text-align: left;
    padding-right: 2%;
    padding-left:2%;
}

a {
    text-decoration: none;
}

/*
    p√°gina
*/

.mainpage {
    font-family: Arial, Helvetica, sans-serif;
    margin: 0 5% 0 5%;
    text-align: center;
    font-size: 1.2em;
}

.intro{
    margin-top: 10%;
}

.obs{
    font-size: 0.8em;
}

.projectitem{
    margin-top: 5%;
}

.projectitemspace{
    display: flex;
    justify-content: space-between;
}

.img{
    max-height: 150px;
}

.foot{
    color: black;
    text-align: center;
    font-family: monospace;
    padding:10px;
    font-weight: bold;
}

.location{
    font-size: 0.8em;
    margin-bottom: 10%;
}

/*
    topicos
*/

.topic{
    margin-top: 5%;
    font-family: monospace;
    padding: 2%;
}

.subtopic{
    font-weight: bold;
}

.subsubtopic{
    color: rgb(92, 92, 92);
    margin-right: 20%;
}

.topicfocus{
    font-size: 1.5em;
    font-weight: bold;
    padding: 10px;
    background-color: black;
    color: white;
}

/*
    logos
*/

.logosgroup {
    display: block;
    text-align: center;
  }

.logos {
    cursor:pointer;
    padding:2%;
    font-size: 2em;
    color: #5a5a5a;
}

.logos:hover{
    color: rgb(16, 16, 95);
}

.footname{
    font-size: 0.8em;
}
